#top {
  .header-primary-container {
	position: relative;
	z-index: 1000;
	#mini-account-wrapper-regular {
	  .links {
		li {
		  a {
			font-size: 11px;
		  }
		}
	  }
	}
  }
  @include bp(max-width, 1600px) {
	.nav-regular {
	  li.level0 {
		> a {
		  padding: 0 20px !important;
		  &.no-click {
			padding: 0 0 0 50px !important;
		  }
		}
	  }
	}
  }
  @include bp(max-width, 1300px) {
	.nav-regular {
	  li.level0 {
		> a {
		  &.no-click {
			padding: 0 0 0 50px !important;
		  }
		}
	  }
	}
  }
  @include bp(max-width, 1200px) {
	.nav-regular {
	  li.level0 {
		> a {
		  padding: 0 15px !important;
		  &.no-click {
			padding: 0 0 0 50px !important;
		  }
		}
	  }
	}
  }
  @include bp(max-width, 1100px) {
	.nav-regular {
	  li.level0 {
		> a {
		  padding: 0 10px !important;
		  &.no-click {
			padding: 0 0 0 50px !important;
		  }
		}
	  }
	}
  }
}

#top.header-mobile {
  .header-primary-container {
	#mini-account-wrapper-regular {
	  .links {
		li {
		  a {
			line-height: 44px;
			font-size: 16px;
		  }
		}
	  }
	}
  }
}

.header-container {
  .nav-regular {
	> .nav-item.first {
	  position: relative;

	  @include bp(min-width, 800px) {
		box-sizing: border-box;

		> a {
		  font-size: 1rem;
		  line-height: 1.4;
		  letter-spacing: 2px;
		  padding-left: 50px;
		  box-sizing: border-box;
		  position: relative;
		  background: #132b7f !important;

		  > .caret {
			display: none;
		  }
		}

		> ul {
		  width: 100%;
		}
	  }
	}
  }

  .lang-switcher {
	&.dropdown {
	  .dropdown-content {
		min-width: 100%;
		box-sizing: border-box;
		padding: 0 10px 5px;

		> li .label {
		  margin-right: 4px;
		}
	  }
	}

	.caret {
	  &:before {
		display: none;
	  }
	}
  }

  .hp-block.right-column {
	.item {
	  &:nth-child(2) {
		float: left;
		margin-left: 20%;

		@include bp(min-width, 1560px) {
		  margin-left: 26%;
		}

		@include bp(max-width, 1199px) {
		  margin-left: 15%;
		}

		@include bp(max-width, 769px) {
		  float: none;
		}
	  }

	  @include bp(max-width, 1140px) {
		margin-left: 5% !important;
	  }

	  @include bp(max-width, 991px) {
		margin-left: 3% !important;
	  }

	  @include bp(max-width, 840px) {
		margin-left: 0 !important;
	  }
	}
  }

  .dropdown.header-contacts {
	padding-top: 10px;

	@include bp(max-width, 1140px) {
	  margin-top: 22px;
	  min-width: 153px;
	  padding-top: 0;
	}

	a {
	  font-size: 1rem;
	  letter-spacing: 1px;
	  font-weight: 500;
	  cursor: auto;

	  @include bp(max-width, 991px) {
		letter-spacing: 0;
	  }
	}

	.caret {
	  &:before {
		display: none;
	  }
	}

	.dropdown-content {
	  min-width: 100%;
	  box-sizing: border-box;
	  padding: 2px 13px 2px 17px;

	  li {
		margin-bottom: 8px;
		white-space: normal;
		clear: both;
		padding-left: 2px;
	  }

	  .operator-icon {
		display: none;
		float: left;
		margin: 0 8px 8px 0;
	  }

	  .content {
		display: block;
		overflow: hidden;
		margin-bottom: 8px;
	  }
	}
  }
}

